@import url('https://fonts.googleapis.com/css?family=Fjalla+One|Fredoka+One|Permanent+Marker|Sedgwick+Ave&display=swap');
/* 
font-family: 'Permanent Marker', cursive;
font-family: 'Fjalla One', sans-serif;
font-family: 'Sedgwick Ave', cursive;
font-family: 'Fredoka One', cursive;
*/

/* fur butlr fonts */

.font1{
font-family: 'Fredoka One', cursive;
}
.font2{
  font-family: 'Sedgwick Ave', cursive;
}
.font3{
  font-family: 'Fjalla One', cursive
}

/* universe styles */
html {
  height: 100%;
  margin: 0;
}
body {
  background-image: url("../src/images/site/pet_pattern.png");
  /* background-color: #f88e78; */
}
.main-content {
  height: 100%;
}

p.main-text {
  font-size: 3em;
}
.right-margin-0 {
  margin-right: 0;
}
.left-margin-0 {
  margin-left: 0;
}

.top-margin-0 {
  margin-top: 0;
}

.content-padding {
  padding: 20px;
}

.max-height{
  min-height: 1000% !important;
}

.font-200{
  font-size: 150% !important;
}

/* For Testing MediaQuery */
/* .container {
  border: 1px solid;
} */

/* index styles */

.job-box {
  height:350px;
  width:100%;
  background-position: center;
  background-size: cover;
  border-radius: 10px !important;
}

/* results styles */

.result-profile-image {
  width:200px;
  height:200px;
  background-position: center;
  background-size: cover;
}

/* profile styles */

#profile-header {
  width: 100%;
  height: 250px;
  background-image: url("../src/images/site/profile-header.png");
  background-position: center;
  background-size: cover;
}

.pet-image {
  width:200px;
  height:200px;
  background-position: center;
  background-size: cover;
}

.profile-image {
  width:100%;
  height:200px;
  background-position: center;
  background-size: cover;
}

/* change profile styles */

.long-text-box {
  height: 200px !important;
}

.select-dropdown, .dropdown-trigger {

}

.margin-right-5 {
  margin-right: 5px;
}
/* fur butlr colors */

.butlr-pink {
  background-color: #f88e78 !important;
}

.butlr-green {
  background-color: #02c9a8 !important;
}

.butlr-blue {
  background-color: #224e82 !important;
}

.butlr-yellow {
  background-color: #ffff99 !important;
}

.butlr-pink-text {
  color: #f88e78 !important;
}

.butlr-green-text {
  color: #02c9a8 !important;
}

.butlr-blue-text {
  color: #224e82 !important;
}

.butlr-yellow-text {
  color: #ffff99 !important;
}

.card,.card-image {
  border-radius: 10px !important;
}
.card-action {
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
}
.brand-logo {
  margin-top: 5px;
  margin-left: 10px;
}

/* user nav styles */
#profile-nav-image {
  width:40px;
  height:40px;  
  background-position: center;
  background-size: cover;
  position: relative;
  top:12px;
  border-radius: 5px;
}

nav {

  padding-top: 10px;
  padding-bottom: 20px;
  margin-bottom: 20px;
}
.nav-wrapper {
  padding-left: 2em;
  padding-right: 2em;
  font-size: 200%!important;

}
